.glide {
  position: relative;
  width: 100%;
  box-sizing: border-box;
  &--rtl {
    direction: rtl;
  }
  &__track {
    overflow: hidden;
  }
  &__slides {
    position: relative;
    width: 100%;
    list-style: none;
    backface-visibility: hidden;
    transform-style: preserve-3d;
    touch-action: pan-Y;
    overflow: hidden;
    padding: 0;
    white-space: nowrap;
    display: flex;
    flex-wrap: nowrap;
    will-change: transform;
  }
  &__slides--dragging {
    user-select: none;
  }
  &__slide {
    width: 100%;
    height: 100%;
    flex-shrink: 0;
    white-space: normal;
    user-select: none;
    -webkit-touch-callout: none;
    -webkit-tap-highlight-color: transparent;
  }
  &__slide a {
    user-select: none;
    -webkit-user-drag: none;
    -moz-user-select: none;
    -ms-user-select: none;
  }
  &__arrows {
    -webkit-touch-callout: none;
    user-select: none;
  }
  &__bullets {
    -webkit-touch-callout: none;
    user-select: none;
  }
  &__arrow {
    position: absolute;
    display: block;
    top: 47%;
    z-index: 2;
    color: white;
    cursor: pointer;
    transform: translateY(-50%);
    @include size(40px);
    border: 1px solid;
    border-radius: 50%;
    @include flex(center, center, row);
    transition: color 0.4s ease;
    &__left {
        @include size(6px);
        border-right: 1px solid;
        border-bottom: 1px solid;
        transform: rotate(45deg);
    }
    &__right {
        @include size(6px);
        border-right: 1px solid;
        border-bottom: 1px solid;
        transform: rotate(45deg);
    }
  }
  &__arrow:focus {
    outline: none;
  }
  &__arrow:hover {
    border-color: white;
  }
  &__arrow--left {
    left: -8em;
  }
  &__arrow--right {
    right: -8em;
  }
  &__arrow--disabled {
    opacity: 0.33;
  }
  
  &__bullets {
    position: absolute;
    z-index: 2;
    left: 50%;
    display: inline-flex;
    list-style: none;
    transform: translateX(-50%);
    margin-top: 60px;
  }
  &__bullet {
    width: 9px;
    height: 9px;
    padding: 0;
    border-radius: 50%;
    border: 1px solid $WHITE;
    transition: all 100ms ease-in-out;
    cursor: pointer;
    line-height: 0;
    box-shadow: 0 0.25em 0.5em 0 rgba(0, 0, 0, 0.1);
    margin: 0 25px
  }
  &__bullet:focus {
    outline: none;
  }
  &__bullet:hover,
  &__bullet:focus {
    border: 2px solid white;
    background-color:$WHITE;
  }
  &__bullet--active {
    background-color: white;
  }
  &--swipeable {
    cursor: grab;
    cursor: -moz-grab;
    cursor: -webkit-grab;
  }
  &--dragging {
    cursor: grabbing;
    cursor: -moz-grabbing;
    cursor: -webkit-grabbing;
  }
}
.glide * {
  box-sizing: inherit;
}