.button {
    cursor: pointer;
    display: block;
    @include text($H14, 400, $TITLE-COLOR);
    font-family: $SECONDARY-FONT;
    text-transform: uppercase;
    width: 100%;
    @include media {
        width: 190px;
    }
    height: 50px;
    border: 1px solid $TITLE-COLOR;
    @include media {
        margin-bottom: 60px;
    }
    margin-bottom: 20px;
    transition: box-shadow 0.4s ease,
                background-color 0.2s ease;
    @include hover {
        box-shadow: 2px 2px 2px black;
    }
    &:active {
        background-color: lighten($color: #A2A0B7, $amount: 10%);
        box-shadow: 4px 4px 2px black;
    } 
} 